.container{
  width:90%;
  padding:20px;
  margin:100px auto;
  display:flex;
  flex-direction:row;
  justify-content: center;
  margin-bottom: 20px;
}

.box{
  width:400px;
  margin:0 10px;
  box-shadow: 0 0 20px 2px rgba(0, 0, 0, .1);
  transition:ls;
}

.box img{
  display: block;
  width:100%;
  border-radius: 5px;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
